import { makeStyles } from '@mui/styles';
import { styled } from '@mui/material/styles';
import { Icon } from '@iconify/react';
import {
  Box,
  Grid,
  Card,
  Stack,
  Typography,
  Table,
  Avatar,
  TableRow,
  TableBody,
  TableCell,
  TableHead,
  TableContainer
} from '@mui/material';
import { TaskHeader, EachTask } from './';
import Scrollbar from '../../../Scrollbar';
import LinearProgress, { linearProgressClasses } from '@mui/material/LinearProgress';
// ----------------------------------------------------------------------

const useStyles = makeStyles({
  table: {
    borderCollapse: 'separate',
    borderSpacing: '0px 20px'
    // marginTop: 0
  },
  tableCell: {
    cursor: 'pointer',
    borderTop: '1px solid #00000010',
    borderBottom: '1px solid #00000010',
    padding: 5
  },
  tableHead: {
    fontSize: '12px',
    color: '#63738160',
    backgroundColor: 'white',
    marginLeft: 0,
    paddingBottom: 0,
    paddingTop: 0,
    paddingLeft: '20px',
    height: '10px'
  }
});

const MOCK_SALES = [...Array(3)].map((_, index) => ({
  id: 1,
  task_name: 'Complete Designs for all Screen',
  status: 'In Progress',
  enquiry: '#225561',
  date: '/static/mock-images/avatars/avatar_14.jpg'
}));

const BorderLinearProgress = styled(LinearProgress)(({ theme }) => ({
  height: 10,
  borderRadius: 5,
  [`&.${linearProgressClasses.colorPrimary}`]: {
    backgroundColor: theme.palette.grey[theme.palette.mode === 'light' ? 200 : 800]
  },
  [`& .${linearProgressClasses.bar}`]: {
    borderRadius: 5,
    backgroundColor: theme.palette.mode === 'light' ? '#27C59A' : '#308fe8'
  }
}));

export default function ListView() {
  const classes = useStyles();

  return (
    <Scrollbar>
      <TableContainer sx={{ minWidth: 720 }}>
        <Table className={classes.table}>
          <TableHead>
            <TableRow>
              <TableCell className={classes.tableHead}>
                <Stack direction="row" alignItems="center" spacing={1}>
                  <Icon icon="carbon:add-alt" color="#979797" width="16" height="16" />
                  <Typography variant="subtitle2" sx={{ fontSize: '12px', color: '#63738160' }}>
                    Task Name
                  </Typography>
                </Stack>
              </TableCell>
              <TableCell className={classes.tableHead}>Status</TableCell>
              <TableCell className={classes.tableHead}>Enquiry</TableCell>
              <TableCell className={classes.tableHead}>Dates</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {MOCK_SALES.map((row) => (
              <TableRow key={row.task_name}>
                <TableCell
                  className={classes.tableCell}
                  sx={{ borderLeft: '1px solid #00000010', borderBottomLeftRadius: '8px', borderTopLeftRadius: '8px' }}
                >
                  <Stack direction="row" spacing={2}>
                    <Icon icon="mdi:drag-vertical" color="#979797" height="24" />
                    <Typography variant="subtitle2"> {row.task_name}</Typography>
                  </Stack>
                </TableCell>
                <TableCell className={classes.tableCell}>
                  <Icon icon="ci:dot-04-l" color="#f2994a" style={{ marginRight: 10 }} />
                  {row.status}
                </TableCell>
                <TableCell className={classes.tableCell}>
                  <Icon icon="ci:dot-04-l" color="#f2994a" style={{ marginRight: 10 }} />
                  {row.enquiry}
                </TableCell>
                <TableCell
                  className={classes.tableCell}
                  sx={{
                    borderRight: '1px solid #00000010',
                    borderBottomRightRadius: '8px',
                    borderTopRightRadius: '8px'
                  }}
                >
                  <Stack direction="row" justifyContent="space-between">
                    <Avatar alt="My Avatar" src={row.date} style={{ width: '32px', height: '32px' }} />
                    <Stack>
                      <Typography variant="subtitle2"> 25th Oct - 30th Oct</Typography>
                      <BorderLinearProgress variant="determinate" value={50} />
                    </Stack>
                    <Icon icon="mdi:dots-vertical" color="#212B36" width="24" height="24" />
                  </Stack>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </Scrollbar>
  );
}
