import { useState, useEffect } from 'react';
// material
import { Container, Typography, Grid, Stack, Tab, Tabs, Box } from '@mui/material';
// hooks
import useSettings from '../hooks/useSettings';
// components
import { Icon } from '@iconify/react';
import { capitalCase } from 'change-case';
import Page from '../components/Page';
import { SearchTasks, TasksFilter, AddTaskButton } from '../components/_dashboard/general-tasks';
import { ListView } from '../components/_dashboard/general-tasks/ListHead';
// ----------------------------------------------------------------------

export default function PageTwo() {
  const { themeStretch } = useSettings();
  const [viewModal, setViewModal] = useState(false);
  useEffect(() => {
    console.log('viewModal: ', viewModal);
  });

  const handleModal = () => {
    setViewModal(!viewModal);
  };
  const handleClose = () => {
    setViewModal(false);
  };

  const [currentTab, setCurrentTab] = useState('List View');
  // const dispatch = useDispatch();

  // useEffect(() => {
  //   dispatch(getCards());
  //   dispatch(getAddressBook());
  //   dispatch(getInvoices());
  //   dispatch(getNotifications());
  //   dispatch(getProfile());
  // }, [dispatch]);

  const ACCOUNT_TABS = [
    {
      value: 'List View',
      icon: <Icon icon="mdi:notification-clear-all" width="20" height="20" />,
      component: <ListView />
    },
    {
      value: 'Calender View',
      icon: <Icon icon="mdi:chevron-right" width={20} height={20} />,
      component: <ListView />
    },
    {
      value: 'Workload',
      icon: <Icon icon="ep:clock" width={20} height={20} />,
      component: <ListView />
    },
    {
      value: 'User View',
      icon: <Icon icon="carbon:add" color="#212b36" width="20" height="20" />,
      component: <ListView />
    }
  ];

  const handleChangeTab = (event, newValue) => {
    setCurrentTab(newValue);
  };

  return (
    <Page title="Tasks" sx={{ fontFamily: 'inter' }}>
      <Container maxWidth={themeStretch ? false : 'xl'}>
        <Stack direction={{ xs: 'column', sm: 'row' }}>
          <Stack sx={{ flexGrow: 1 }}>
            <Typography variant="subtitle1" sx={{ fontSize: '32px', lineHeight: '48px', fontWeight: '900' }}>
              Tasks
            </Typography>
          </Stack>
          <Stack>
            <AddTaskButton handleModal={handleModal} />
          </Stack>
        </Stack>
        <Stack spacing={2}>
          <Stack direction="row" spacing={4}>
            <Stack
              direction={{ xs: 'column', sm: 'row' }}
              justifyContent="space-between"
              sx={{ borderBottom: '1px solid #B0B6BE', pb: 1, flexGrow: 1 }}
            >
              <Stack>
                <Tabs
                  value={currentTab}
                  scrollButtons="auto"
                  variant="scrollable"
                  allowScrollButtonsMobile
                  onChange={handleChangeTab}
                >
                  {ACCOUNT_TABS.map((tab) => (
                    <Tab
                      disableRipple
                      key={tab.value}
                      label={capitalCase(tab.value)}
                      icon={tab.icon}
                      value={tab.value}
                    />
                  ))}
                </Tabs>
              </Stack>
              <Stack>
                <SearchTasks />
              </Stack>
            </Stack>

            <Stack>
              <TasksFilter />
            </Stack>
          </Stack>
          <Stack sx={{ flexGrow: 1 }}>
            {ACCOUNT_TABS.map((tab) => {
              const isMatched = tab.value === currentTab;
              return isMatched && <Box key={tab.value}>{tab.component}</Box>;
            })}
          </Stack>
        </Stack>
      </Container>
    </Page>
  );
}
