// material
import { Button } from '@mui/material';

export default function AddTasksButton(props) {
  const { handleModal } = props;

  return (
    <Button
      variant="contained"
      sx={{
        color: '#F6F6F7',
        backgroundColor: '#B98900',
        boxShadow: '0 0px 0px #fff',
        pt: '11px',
        pb: '14px',
        px: '54px',
        '&:hover': {
          backgroundColor: '#B98900'
        },
        fontSize: '14px',
        lineHeight: '17px',
        fontWeight: '500',
        fontFamily: 'inter'
      }}
      onClick={handleModal}
    >
      Add Task
    </Button>
  );
}
